.top-section {
  position: relative;
  background-color: $section-background;

  height: 900px;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;

  @include onSmallDesktop {
    height: 800px;
  }

  &__title {
    font-weight: 600;
    font-size: 52px;
    line-height: 120%;
    letter-spacing: -2px;
    color: #fff;

    margin: 50px 0 0;

    @include onTablet {
      font-size: 64px;
      line-height: 68px;

      margin: 58px 0 0;
    }

    @include onSmallDesktop {
      width: min-content;

      margin-top: 166px;
    }
  }

  &__paragrahp {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #fff;

    margin: 32px 0 0;

    @include onTablet {
      width: 362px;
      margin: 24px 0 0;
    }

    @include onSmallDesktop {
      margin-top: 32px;
    }
  }

  &__button {
    width: 280px;
    height: 50px;

    margin: 72px 0 0;

    @include onTablet {
      width: 264px;
      margin: 56px 0 0;
    }

    @include onSmallDesktop {
      margin-top: 82px;
    }
  }

  &__slider {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;

    @include onSmallDesktop {
      right: 0;
      bottom: 0;
      left: 50%;
    }

    @include onDesktop {
      left: 55%;
    }
  }
}
