.testimonials-card {
  display: flex;
  flex-direction: column;
  align-items: center;

  background: #fff;
  box-shadow: 0 9px 18px rgba(37, 41, 49, 0.03);
  border-radius: 16px;

  padding: 32px 42px;

  transition: transform 1.3s;

  &:hover {
    transform: scale(1.05);
  }

  @include onTablet {
    padding: 32px 98px;
  }

  @include onSmallDesktop {
    padding: 32px 48px;
  }

  @include onDesktop {
    padding: 32px 56px;
  }

  &__image-block {
    position: relative;
    width: 196px;
    height: 196px;

    @include onTablet {
      width: 250px;
      height: 250px;
    }
  }

  &__rectangle {
    position: absolute;

    width: 86px;
    height: 110px;

    @include onTablet {
      width: 110px;
      height: 142px;
    }

    &--1 {
      top: 4px;
      left: 12px;

      @include onTablet {
        left: 18px;
      }
    }

    &--2 {
      bottom: 4px;
      right: 12px;

      @include onTablet {
        right: 18px;
      }
    }
  }

  &__image {
    position: absolute;
    width: 110px;
    height: 110px;

    border-radius: 50%;
    margin: 44px;

    @include onTablet {
      width: 140px;
      height: 140px;

      margin: 55px;
    }
  }

  &__quotes {
    margin: 28px 0 0;
  }

  &__coment {
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;
    text-align: center;

    color: $paragraph-color;

    margin: 12px 0 0;

    @include onTablet {
      margin: 18px 0 0;
    }
  }

  &__name {
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;

    color: $title-grey;

    margin: 16px 0 0;
  }

  &__position {
    font-weight: 700;
    font-size: 12px;
    line-height: 20px;

    letter-spacing: 3px;
    text-transform: uppercase;

    color: #6c788b;

    margin: 4px 0 0;
  }
}
