.about-us {
  &__content {
    margin: 120px 0 0;

    @include onTablet {
      margin: 148px 0 0;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;

    color: $title-grey;

    text-align: center;
  }

  &__paragraph {
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    color: $paragraph-color;

    text-align: center;

    margin: 24px 0 0;

    @include onTablet {
      margin: 16px 0 0;
    }
  }
}
