.testimonials {
  &__content {
    margin: 120px 0 0;

    @include onTablet {
      margin: 148px 0 0;
    }

    @include onSmallDesktop {
      margin: 152px 0 0;
    }
  }

  &__main-title {
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 3px;
    text-transform: uppercase;

    color: $section-title;
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;

    color: $title-grey;

    margin: 16px 0 0;
  }

  &__card-container {
    display: flex;
    flex-direction: column;
    gap: 24px;

    margin: 48px 0 0;

    @include onTablet {
      gap: 30px;

      margin: 56px 0 0;
    }

    @include onSmallDesktop {
      flex-direction: row;
      justify-content: center;
      gap: 20px;

      margin: 86px 0 0;
    }

    @include onDesktop {
      gap: 30px;
    }
  }

  &__card {
    min-width: 280px;

    @include onSmallDesktop {
      width: 292px;
    }

    @include onDesktop {
      width: 362px;
    }
  }
}
